    /**
     * 检测是否庄的牌堆
     * @param groupName 
     */
    public static checkIsBanker(groupName: string){
        return groupName.indexOf("dealer") > -1;
    }

    /**
     * 检测是否炸金花的牌堆
     * @param groupName 
     */
    public static checkZjhGroup(groupName: string){
        return groupName.indexOf("zjh") > -1;
    }

    /**根据牌堆类型返回牌型音效配置 */
    public static getGroupTypeSoundArr(groupName: string){
        if (this.checkZjhGroup(groupName))
            return BbwzPokerTool.zjhPokerTypeSound;
        else
            return BbwzPokerTool.bullPokerTypeSound;
    }

